import React from "react";
import { useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import SwiperCore, { Autoplay } from "swiper";
import "./css/cardNews.css";

const images = [
  "./img/card1.png",
  "./img/card2.png",
  "./img/card3.png",
  "./img/card4.png",
  "./img/card5.png",
  "./img/card6.png",
  "./img/card7.png",
  "./img/card8.png",
  "./img/card1.png",
  "./img/card2.png",
  "./img/card3.png",
  "./img/card4.png",
  "./img/card5.png",
  "./img/card6.png",
  "./img/card7.png",
  "./img/card8.png",
];

function CardNews() {
  const [mouseEnter, setMouseEnter] = useState(false);
  const swiperRef = useRef(null);
  const speed = 3000;
  SwiperCore.use([Autoplay]);

  return (
    <div className="cardnews_body">
      <div className="cardnews_title">
        <div className="cardnews_title_big">
          <span className="cardnews_title_quote_open"></span>
          <span>   ?!</span>
          <span className="cardnews_title_quote_close"></span>
        </div>
        <div className="cardnews_title_small">
          <span>  IT </span>
        </div>
      </div>
      <div // onMouseEnter, onMouseLeave   div>
        onMouseEnter={() => {
          setMouseEnter(true);
          swiperRef.current.swiper.autoplay.stop();
        }}
        onMouseLeave={() => {
          setMouseEnter(false);
          swiperRef.current.swiper.autoplay.start();
          setTimeout(() => {
            if (mouseEnter === false) {
              swiperRef.current.swiper.autoplay.start();
            }
          }, speed); //     swiper   autoplay 
        }}
      >
        <Swiper
          ref={swiperRef}
          spaceBetween={25}
          slidesPerView="auto"
          autoplay={{
            delay: 0,
            disableOnInteraction: false, //     
          }}
          loop={true} //  
          speed={speed} //   
          loopedSlides={3}
          centeredSlides //  
          grabCursor //  
          pauseOnMouseEnter={true}
        >
          {images.map((img, index) => (
            <SwiperSlide>
              <img className="cards" src={img} alt={`card${index}`} />
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </div>
  );
}

export default CardNews;
